<script>
 import * as firebase from "firebase/app";

export default {
    name:'PharmacieDetail',

    data(){
        return {
            pharmacie:{},
            messages: this.$store.getters.getMessageListe
        }
    },

    methods: {
        pharmacieSave(){

            firebase.firestore().collection('profil')
                .doc(this.pharmacie.email).set(this.pharmacie)
                .then(()=>{
                    this.$store.commit('setDetailPharmacie',this.pharmacie);
                });

        },
        pharmacieCancel(){
            this.pharmacie=this.$store.getters.profilConnecte;
        },
        pharmacieGarde(){
            this.$router.push("/garde-detail/");
        },
        maPosition(){
            if (navigator.geolocation) {
                       navigator.geolocation.getCurrentPosition((ok)=>{
                       ///success
                            this.pharmacie.latitude= ok.coords.latitude;
                            this.pharmacie.longitude= ok.coords.longitude;
                   },
                   (bad)=>{
                       console.log(bad);
                   });
            } else {
                console.log("Erreur de localisation!")
            }
        },
        scrollToEnd(){
            // var container=document.querySelector("#scroll");
            // var scrollHeight=container.scrollHeight;
            // container.scrollTop=scrollHeight;
        },
    },

    mounted(){
        this.pharmacie=this.$store.getters.getProfilConnecte;
        // this.scrollToEnd();
    }

}
</script>

